	export default {
		data() {
			return {
				isCancel: false,
                info: [],
                num: 0,
                isSelect: false,
                allSelect: '全选',
                selectedId: 0,
                selectedArr: []
			}
		},
		methods: {
            checkboxChange(e){
                console.log(e)
                this.num = e.detail.value.length;
                if (this.num < this.info.length) {
                    this.allSelect = '全选';
                } else {
                    this.allSelect = '取消全选';
                }
            },
            all_select(){
                if (this.isSelect) {
                    this.num = 0;
                    this.isSelect = false;
                    this.allSelect = '全选';
                } else {
                    this.num = this.info.length;
                    this.isSelect = true;
                    this.allSelect = '取消全选';
                }
            }
		},
        onNavigationBarButtonTap() {



















        },
        onLoad() {}
	}
